* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: Roboto, sans-serif;
  display: flex;
}

.header {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 408px;
  padding-top: 32px;
  display: flex;
}

.header__imac {
  content: url("imac.202c52ff.jpeg");
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
}

.header__title {
  color: #060b35;
  align-items: flex-start;
  width: 166px;
  height: 36px;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.main {
  width: 166px;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.main__product {
  color: #616070;
  align-self: start;
}

.reviews {
  justify-content: space-between;
  width: 166px;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.reviews__stars {
  gap: 4px;
  margin-bottom: 8px;
  display: flex;
}

.reviews__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.reviews__stars--0 .reviews__star:nth-child(n+5) {
  filter: grayscale();
}

.reviews__review {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.price {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 18px;
  margin-bottom: 16px;
  display: flex;
}

.price__title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
}

.price__money {
  font-size: 16px;
  font-weight: 700;
}

.button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.button:hover {
  color: #00acdc;
  background: #fff;
  border: 1px solid #00acdc;
}
